<template>
  <div class="page">
    <Markdown :source="mdCode"></Markdown>
  </div>
</template>

<script>
import Markdown from 'vue3-markdown-it'
import Api from '../api'
import formatMdVar from '../api/formatMdVars'
import generateMdPath from '../api/generateMdPath'

export default {
  name: 'DocsMethod',
  components: {
    Markdown
  },
  data () {
    return {
      mdCode: '# Page: Method'
    }
  },
  mounted () {
    this.initMdFile()
  },
  methods: {
    initMdFile () {
      const mdPath = generateMdPath(this)
      if (mdPath === 'file_error_404') {
        this.$router.push({ name: 'Main' })
        return false
      }
      Api.mdGetMain(mdPath).then(({ data }) => {
        if (data && data !== '') this.mdCode = formatMdVar(this.$store.getters.documentation.vars, data)
        else this.$router.push({ name: 'Main' })
      }).catch(() => {
        this.$router.push({ name: 'Main' })
      })
    }
  },
  watch: {
    '$route.params' () {
      this.initMdFile()
    }
  }
}
</script>
